*{
    font-family: Poppins;
}
@media only screen and (min-width: 360px)
{
    body{
        margin: 0px;
        background-image: url("./images/background.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .nav-container{
        display:flex;
        justify-content: center;
    }
    .logo{
        width: 20%;
    }
    .hero-images{
        display:none;
    }
    .hero-text{
        text-align: center;
        margin: 40% 7%;
        padding: 5%;
        background: rgba(255, 255, 255, 0.4);
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
    }
    .bold {
        font-weight: bold;
    }
    .gray {
        color: #5f5f5f;
    }
    .card-main-container{
        width: 100%;
        height: 500px;
        overflow-x: scroll;
        overflow-y: hidden;
    }
    .cards{
        display:flex;
        justify-content: space-around; 
        width: 1000px;
        height: 100%;  
        padding: 5%;  
    }
    .card{
        min-width: 150px;
        width: 250px;
        height: fit-content;
        border-radius: 10px;
        font-size: 1rem;
        background: rgba(255, 255, 255, 0.4);
        box-shadow: 0px 5px 20px 5px #000000;
        flex: 0 0 auto;         
        display: flex;          
        flex-direction: column;
        position:relative;
    }
    .card-image{
        width: 250px;
        height: 300px;
        border-radius: 10px;
    }
    .card-title {
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .card-stats, .card-text, .card-text-price{
        padding-left:10px;
    }
    .card-text-price{
        font-size:small;
    }
    .card-badge {
        position: absolute;
        background-color: white;
        padding:5px;
        margin:10px;
        font-size: larger;
        font-weight: bold;
    }
}
@media only screen and (min-width: 768px)
{
    .hero-text{
        margin: 20% 10%;
    }
}
@media only screen and (min-width: 1024px)
{
    body{
        background-image: none;
        background-color: lightgray;
    }
    .app{
        width: 90%;
        margin: 5% auto 0 auto;
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
        background-image: url("./images/background.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .nav-container{
        border-radius: 10px;
    }
    .hero-images{
        display: flex;
        justify-content: center;
        background: rgba(255, 255, 255, 0.4);
        margin: 10% 10%;
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
    }
    .hero-images-column-1,.hero-images-column-2,.hero-images-column-3,.hero-images-column-4,.hero-images-column-5{
        width: 20%;
    }
    .image{
        display: block;
        width: 90%;
        height: 40%;
        margin: 10% auto;
        border-radius: 10px;
        border: 1px solid grey;
        object-fit: cover;
    }
    .image-additional{
        height: 30%;
    }
    .hero-images-column-1{
        margin-top: 25%;
    }
    .hero-images-column-2{
        margin-top: 10%;
    }
    .hero-images-column-3{
        margin-top: 20%;
    }
    .hero-images-column-4{
        margin-top: 15%;
    }
    .hero-images-column-5{
        margin-top: 30%;
    } 
    .card-main-container{
        overflow-x:initial;
        overflow-y:initial;
    }
    .cards{
        width: 100%;
        padding: 0;
    }
    .card{
        margin: 0 auto;
        padding: 0;
    }
}
@media only screen and (min-width: 1366px)
{
}


@media only screen and (min-width: 1920px){ /*For big screens*/
   .card{
       width: 30%;
       display: grid;
   }
   .card-image, .card-stats, .card-text, .card-text-price{
       margin: 0 auto;
   }
}